.section-company {
    .card-img {
        text-align: center;
        padding: 20px 0;
        i {
            font-size: 40px;
            color: #ffcd05;
        }
    }
}
.company-text {
    font-size: 16px;
    color: $gray;
    font-weight: 600;
    width: 45%;
    text-align: center;
    margin: 0 auto;
}
.section-title {
    text-align: center;
}

.img-holder {
    padding: 10px;
    position: relative;

    &:before {
        content: 'Product Name';
        @include animate(opacity);
        color: $white;
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        visibility: hidden;
        opacity: 0;
        // display: none;
        justify-content: center;
        align-items: center;
    }
    &:hover {
        &:before {
            display: flex;
            opacity: 1;
            visibility: visible;
            background: rgba($color: #000000, $alpha: 0.5);
        }
    }
}