    #header {
  position: fixed;
  background: rgba($color: #000000, $alpha: 0.4);
  padding: 20px 0;
  z-index: 2;
  top: 0;
  left: 0;
  width: 100%;
}

.header-holder {
  display: flex;
}

.logo {
  width: 100px;
  min-width: 100px;
  margin-right: 20px;
}

#nav {
  @extend %clearfix;
  flex-grow: 1;
}

.nav-drop {
  @include animate(all);
  padding-top: 9%;
  background: $mobile-nav-drop-bg;
  color: $mobile-nav-drop-color;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  z-index: 999;
  opacity: 0;
  visibility: hidden;

  .nav-active & {
    opacity: 1;
    visibility: visible;
    height: 100%;
    position: fixed;
    overflow: hidden;

  }

  > ul {
    @include media('≥tablet') {
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
    }
  }

  li {
    position: relative;
    list-style: none;
    transform: skew(25deg);

    + li {
      padding-left: 20px;
    }

    &.active {
      a {
        opacity: 0.5;
      }
    }

    &:hover {
      >a {
        opacity: 0.5;
      }

      > ul {
        display: block;
      }
    }

    > ul {
      display: none;
      width: 100%;
      top: 100%;
      left: 0;
      z-index: 2;

      @include media('≥tablet') {
        position: absolute;
      }
    }
  }

  a {
    color: inherit;
  }
}

.nav-opener {
  position: relative;
  width: 60px;
  height: 44px;
  font-size: 0;
  line-height: 0;
  float: right;
  z-index: 1000;


  &:before,
  &:after,
  span {
    @include animate(transform);
    background: $white;
    position: absolute;
    top: 20px;
    left: 15%;
    right: 15%;
    height: 5px;
    margin-top: 0;
  }

  &:before,
  &:after {
    content: '';
    top: 10px;
  }

  &:after {
    top: 30px;
  }

  &:hover {
    opacity: 0.9;
  }

  .nav-active & {
    span {
      opacity: 0;
    }

    &:after,
    &:before {
      transform: rotate(45deg);
      top: 16px;
      left: 15%;
      right: 15%;
    }

    &:after {
      transform: rotate(-45deg);
    }
  }
}

.primary-menu {
  max-width: 1000px;
  margin: 0 auto;
  display: flex;
  height: 200px;
  background: rgba($color: #ffcd05, $alpha: 0.5);
  align-items: center;
  transform: skew(-25deg);
}

.resize-active .nav-drop,
.resize-active .nav-drop > ul {
  transition:none !important;
}
