.section-banner {
	@include v-center(400px);
	margin-top: 148px;
	background-size: cover;
	background-position: center;
	padding: 50px 0;

	.banner-text {
		color: $banner-color;
		padding: 0 50px 0 0;
		@include media('≥tablet') {
			max-width: 70%;
		}
	}

	.title {
		margin-bottom: $title-margin-bottom;
	}

	.banner-holder {
		display: flex;
		align-items: center;
		justify-content: center;
	}
	.input-holder {
		display: flex;
		input[type='tel'] {
			margin: 0;
			border: 0;
			width: 50%;
			border-radius: 5px 0 0 5px;
			height: 50px;
		}

		.btn {
			border-radius: 0 5px 5px 0;
			height: 50px;
		}
	}
}